.content{
    width: 100%;
    height: 100%;
    top: 0;
    .content-body{
        height: 100vh*2.3;
        width: 100%;
        position: absolute;
        top: 100px;
        min-width: 1500px;
        display: flex;
        justify-content: center;
        min-height:1900px; 
        .body-content{
            width: 1100px;
            height: 100%;
            position: relative;
            background: white;
            margin-top: 158px;
            .content-box1{
                width: 94%;
                height: 22%;
                margin:0 auto;
                .content-box1-font{
                    width: 100%;
                    height: 20%;
                    margin-top: 30px;
                }
                .content-box1-box{
                    background: #f4f4f4;
                    width: 100%;
                    height: 80%;
                    border: 1px solid #e3e3e3;
                    padding: 15px 20px 0 25px;
                    margin-bottom: 15px;
                    box-sizing: border-box;
                    .box-row1{
                        height: 10%;
                        color: rgb(119, 119, 119);
                    }
                    .box-row2{
                        height: 50%;
                        display: flex;
                        justify-content: center;
                        box-sizing: border-box;
                        ul{
                            margin-top: 2%;
                            list-style: none;
                            width: 90%;
                            height: 80%;
                            display: flex;
                            flex-wrap: wrap;
                            li{
                                width: 5%;
                                height: 33%;
                                background-repeat:no-repeat; background-size:100% 100%;-moz-background-size:100%  100%;
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                font-size: 20px;
                                font-weight: bold;
                                text-shadow: 1px 2px 0px black;
                                color: white;
                                span{
                                    margin-right: 3px;
                                }
                            }
                        }
                    }
                    .box-row3{
                        width: 100%;
                        height: 40%;
                        background-size: contain;
                        background-repeat: no-repeat;
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        .box-row3-span{
                            font-size: 35px;
                            color: rgb(113, 180, 6);
                        }
                        .box-row3-div{
                            width: 25%;
                            margin-top: 3%;
                            height: 80%;
                            margin-right: 20px;
                            text-align: end;
                            span{
                                width: 100%;
                                height: 20%;
                                
                            }
                            ul{
                                list-style: none;
                                width: 100%;
                                height: 65%;
                                display: flex;
                                li{
                                    width: 30%;
                                    height: 90%;
                                    background: rgb(229, 229, 229);
                                    margin-left: 3%;
                                    display: flex;
                                    flex-direction: column;
                                    :nth-child(1){
                                        width: 100%;
                                        margin-top: 10%;
                                        height: 50%;
                                        color: rgb(113, 180, 6);
                                        font-size: 25px;
                                        font-weight: bold;
                                        text-align: center;
                                    }
                                    :nth-child(2){
                                        width: 100%;
                                        height: 40%;
                                        text-align: center;
                                    }
                                }
                            }
                        }
                    }
                }
            }
            .content-box2{
                width: 94%;
                height: 10%;
                margin:0 auto;
                margin-top: 30px;
                margin-bottom: 30px;
                .content-box2-font{
                    width: 100%;
                    height: 30%;
                }
                .content-box2-box{
                    background: #f4f4f4;
                    width: 100%;
                    height: 70%;
                    border: 1px solid #e3e3e3;
                    box-sizing: border-box;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    select{
                        width: 10%;
                        height: 25%;
                        background: rgb(229, 229, 229);
                        border: transparent;
                        outline: none;
                        margin-left: 2%;
                        cursor:pointer
                    }
                    :nth-child(5){
                        margin-left: 10%;
                    }
                    :nth-child(6){
                        width: 20%;
                    }
                    :nth-child(7){
                        width: 13%;
                        height:20%;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        margin-left: 2%;
                        border-radius: 3px;
                        font-size: 14px;
                        text-align: center;
                        background: rgb(0, 168, 224);
                        color: white;
                        font-weight: bold;
                        cursor:pointer
                    }
                }
            }
            .content-box3{
                width: 94%;
                height: 50%;
                margin: 0 auto;
               
                .content-box3-title{
                    width: 100%;
                    height: 6%;
                    background: #f4f4f4;
                    border: 1px solid #e3e3e3;
                    box-sizing: border-box;
                    display: flex;
                    align-items: center;
                    justify-content: flex-start;
                    font-size: 20px;
                    span{
                        text-align: center;
                    }
                    :nth-child(1){
                        width: 20%;
                    }
                    :nth-child(2){
                        width: 75%;
                    }
                }
                .content-box3-list{
                    display: flex;
                    align-items: center;
                    width: 98%;
                    margin-left: 1%;
                    height: 11%;
                    border-bottom: 1px solid #e3e3e3;
                    margin-bottom: 4px;
                    :nth-child(1){
                        width: 20%;
                        text-align: center;
                    }
                    :nth-child(2){
                        height: 100%;
                        width: 75%;
                        ul{
                            list-style: none;
                            width: 100%;
                            height: 100%;
                            display: flex;
                            flex-wrap: wrap;
                            
                            li{
                                width: 5%;
                                height: 33%;
                                background-repeat:no-repeat; background-size:100% 100%;-moz-background-size:100%  100%;
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                font-size: 14px;
                                font-weight: bold;
                                text-shadow: 1px 2px 0px black;
                                color: white;
                                
                                span{
                                    width: 80%;
                                    height: 70%;
                                    margin-right: 3px;
                                }
                            }
                        }
                    }
                    .content-box3-list-child3{
                        width:7.5%;
                        height: 80%;
                        background-size: contain;
                        background-repeat: no-repeat;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        font-size: 20px;
                        font-weight: bold;
                        text-shadow: 1px 2px 0px black;
                        color: white;
                        margin-left: 11%;
                        span{
                            margin-right: 15%;
                        }
                    }
                }
                .content-box3-list:nth-child(11){
                    border: 0px;
                }
                .content-box3-page{
                    width: 100%;
                    display: flex;
                    font-size: 20px;
                    height: 40px;
                    align-items: center;
                    justify-content: center;
                    position: absolute;
                    bottom: 5px;
                    left: 0;
                    .pageUp{
                        display: inline-block;
                        width: 30px;
                        height: 30px;
                    }
                    .minPage{
                        display: inline-block;
                        width: 50px;
                        height: 30px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                    }
                    .maxPage{
                         display: inline-block;
                         width: 50px;
                         height: 30px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                    }
                    .pageDown{
                        display: inline-block;
                        width: 30px;
                        height: 30px;
                    }
                }
            }
        }
        .body-left{
            width: 200px;
            height: 100%;
            margin-right: 10px;
            .left-icon{
                width: 100%;
                margin-top: 50px;
            }
            .left-tip{
                width: 100%;
                background: white;
                height: 60px;
                margin-top: 37px;
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 20px;
                cursor: pointer;
                .jiantou{
                    width: 20px;
                    height: 20px;
                    background: url(../../../assets//Index/arrow_Right.png);
                    background-repeat: no-repeat;
                    background-position: 50%;
                } 
            }
            .left-tip:hover{
                color: rgb(113, 180, 6);
                .jiantou{
                    width: 20px;
                    height: 20px;
                    background: url(../../../assets//Index/arrow_Right2.png);
                    background-repeat: no-repeat;
                    background-position: 50%;
                }
            }
            .left-paper1{
                width: 100%;
                background: white;
                margin-top: 20px;
                height:350px;
                display: flex;
                justify-content: center;
                align-items: center;
                .paper-icon{
                    width: 90%;
                }
            }
        }
        .body-right{
            width: 200px;
            height: 100%;
            margin-left: 10px;
            .right-paper1{
                width: 100%;
                background: white;
                margin-top: 158px;
                height:350px;
                display: flex;
                justify-content: center;
                align-items: center;
                .paper-icon{
                    width: 90%;
                }
            }
        }
    }
}